class Prices extends React.Component {
    state = {
        currency: 'GBP'
    }

    render() {
        let list = '';
            //the api returns an object and not an array
        switch(this.state.currency) {
            case 'USD': 
                list = <li className="list-group-item">
                    Bitcoin rate for {this.props.bpi.USD.description} : <span className="badge batge-primary">
                    {this.props.bpi.USD.code}</span> <stong>{this.props.bpi.USD.rate}</stong>
                </li>
                break;
            case 'GBP':
            list = <li className="list-group-item">
                    Bitcoin rate for {this.props.bpi.GBP.description} : <span className="badge batge-primary">
                    {this.props.bpi.GBP.code}</span> <stong>{this.props.bpi.GBP.rate}</stong>
                </li>
                break;
            case 'EUR':
            list = <li className="list-group-item">
                    Bitcoin rate for {this.props.bpi.EUR.description} : <span className="badge batge-primary">
                    {this.props.bpi.EUR.code}</span> <stong>{this.props.bpi.EUR.rate}</stong>
                </li>
                break;

            default: ''    
        }


        return(
            <div>
                <ul className="list-group">
                    {list}
                </ul>
            </div>
        )
    }
};

export default Prices;